import Link from "next/link"
import React from "react"

const A = ({ href, children }) => {
  if (href.startsWith("/")) {
    return (
      <Link href={href}>
        <a className="underline border-black">{children}</a>
      </Link>
    )
  } else {
    return (
      <a className="underline border-black" href={href}>{children}</a>
    )
  }
}

export default A
